import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";

import { environment } from "../../../environments/environment";
import { Subject } from "rxjs";
import { map } from "rxjs/operators";
import { DatePipe } from "@angular/common";

@Injectable({ providedIn: "root" })
export class SalaryService {
    access_token = localStorage.getItem("access_token");
    headers = new HttpHeaders({
        "Content-Type": "application/json",
        Authorization: "Bearer " + this.access_token,
    });
    options = { headers: this.headers };
    salaries = new Subject<any>();
    salary = new Subject<any>();

    constructor(private http: HttpClient, private datePipe: DatePipe) {}

    public getSalariesData() {
        return this.salaries.asObservable();
    }

    public getSalaryData() {
        return this.salary.asObservable();
    }

    get() {
        return this.http
            .get<any>(`${environment.apiUrl}/api/salary`, this.options)
            .pipe(
                map((salaries) => {
                    this.salaries.next(salaries);
                    return salaries;
                })
            );
    }

    findById(salaryId: any) {
        return this.http
            .get<any>(
                `${environment.apiUrl}/api/salary/${salaryId}`,
                this.options
            )
            .pipe(
                map((salary) => {
                    this.salary.next(salary);
                    return salary;
                })
            );
    }

    add() {
        const body = {};
        return this.http
            .post<any>(
                `${environment.apiUrl}/api/salary/create`,
                body,
                this.options
            )
            .pipe(
                map((response) => {
                    return response;
                })
            );
    }

    update(bonus: any, salaryId: any) {
        const body = {
            bonus: bonus,
        };
        return this.http
            .post<any>(
                `${environment.apiUrl}/api/salary/update/${salaryId}`,
                body,
                this.options
            )
            .pipe(
                map((response) => {
                    return response;
                })
            );
    }

    changeLopStatus(status: any, salaryId: any) {
        const body = {
            can_detect_lop: status,
        };
        return this.http
            .post<any>(
                `${environment.apiUrl}/api/salary/change-lop-status/${salaryId}`,
                body,
                this.options
            )
            .pipe(
                map((response) => {
                    return response;
                })
            );
    }

    changeLopStatusForAll(status: any) {
        const date = new Date();
        const from_date = this.datePipe.transform(
            new Date(date.getFullYear(), date.getMonth() - 1, 27),
            "y-M-d"
        );
        const to_date = this.datePipe.transform(
            new Date(date.getFullYear(), date.getMonth(), 26),
            "y-M-d"
        );
        console.log(from_date);
        const body = {
            can_detect_lop: status,
        };
        return this.http
            .post<any>(
                `${environment.apiUrl}/api/salary/change-lop-status/${from_date}/${to_date}`,
                body,
                this.options
            )
            .pipe(
                map((response) => {
                    return response;
                })
            );
    }

    processSalary() {
        const body = {};
        return this.http
            .post<any>(
                `${environment.apiUrl}/api/salary/process`,
                body,
                this.options
            )
            .pipe(
                map((response) => {
                    return response;
                })
            );
    }

    paySalary(salary_id: any) {
        const body = {};
        return this.http
            .post<any>(
                `${environment.apiUrl}/api/salary/process/${salary_id}`,
                body,
                this.options
            )
            .pipe(
                map((response) => {
                    return response;
                })
            );
    }
}
