import { Component, OnInit } from "@angular/core";
import { routerTransition } from "../../router.animations";
import { SalaryService } from "src/app/shared/services/salary.service";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { InfoComponent } from "../transfer/info/info.component";

@Component({
    selector: "app-salary",
    templateUrl: "./salary.component.html",
    styleUrls: ["./salary.component.scss"],
    animations: [routerTransition()],
})
export class SalaryComponent implements OnInit {
    salaries: any;
    error: any;
    canSubmit: boolean = false;
    isEnableGenerateSalary: boolean = new Date().getDate() > 26;
    isEnableProcessSalary: boolean = new Date().getDate() > 26;
    isEnableDetectLop: boolean = new Date().getDate() > 26;
    isEnableRelaxLop: boolean = new Date().getDate() > 26;

    constructor(
        private salaryService: SalaryService,
        private dialogService: NgbModal
    ) {}

    ngOnInit() {
        this.salaryService.get().subscribe(() => {});
        this.salaryService.getSalariesData().subscribe((data) => {
            this.salaries = data.salaries;
        });
    }

    onGenerateSalary() {
        if (!this.isEnableGenerateSalary) {
            this.salaryService.add().subscribe(() => {
                this.salaryService.get().subscribe(() => {});
            });
        }
    }

    onProcessSalary() {
        if (!this.isEnableProcessSalary) {
            this.salaryService.processSalary().subscribe(() => {
                this.salaryService.get().subscribe(() => {});
            });
        }
    }

    paySalary(salary_id: any) {
        if (!this.isEnableProcessSalary) {
            this.salaryService.paySalary(salary_id).subscribe(() => {
                this.salaryService.get().subscribe(() => {});
            });
        }
    }

    onDetectLopForAll() {
        if (!this.isEnableDetectLop) {
            this.salaryService.changeLopStatusForAll(true).subscribe(() => {
                this.salaryService.get().subscribe(() => {});
            });
        }
    }

    onRelaxLopForAll() {
        if (!this.isEnableRelaxLop) {
            this.salaryService.changeLopStatusForAll(false).subscribe(() => {
                this.salaryService.get().subscribe(() => {});
            });
        }
    }

    changeLopStatus(id: any, status: boolean) {
        this.salaryService.changeLopStatus(status, id).subscribe(() => {
            this.salaryService.get().subscribe(() => {});
        });
    }

    updateList(id: number, event: any) {
        if (this.canSubmit) {
            this.salaryService
                .update(event.target.textContent, id)
                .subscribe(() => {
                    this.salaryService.get().subscribe(() => {});
                });
        } else {
            this.salaryService.get().subscribe(() => {});
        }
    }

    changeValue(event: any) {
        const bonus = Number(event.target.textContent);
        if (isNaN(bonus)) {
            this.canSubmit = false;
            this.error = "Bonus must be number";
        } else {
            this.error = "";
            this.canSubmit = true;
        }
    }

    salaryInfo(transferId: any) {
        const modalRef = this.dialogService.open(InfoComponent, {
            size: "lg",
            centered: true,
        });
        modalRef.componentInstance.transferId = transferId;
    }
}
